class Person {
  constructor(name) {
    this.name = name;
    this.favThings = ['oatmeal', 'soup', 'milk'];
  }
  sayHi() {
    console.log(`I, ${this.name} love stuff!`);
    this.favThings.forEach((thing) => {
      console.log(this.beExcited() + thing);
    });
  }
  beExcited() {
    return `I ${this.name} am excited about `;
  }
}

